import { mapState, mapMutations } from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue';
import http from '@/components/utils/http.js';
export default {
	components: {
		uniNumberBox
	},

	data() {
		return {
			title: '购物车',
			userinfo: '',
			total: 0, //总价格
			allChecked: false, //全选状态  true|false
			empty: false, //空白页现实  true|false
			cartList: []
		};
	},
	onLoad() {
		
		if (global.islogon() == false) {
			uni.navigateTo({
				url: '../person/logon'
			});
		}
		this.loadData();
		
	},
	watch: {
		//显示空白页
		cartList(e) {
			
			let empty = e.length === 0 ? true : false;
			if (this.empty !== empty) {
				this.empty = empty;
			}
			
			
		}
	},
	computed: {
		...mapState(['token'])
	},
	methods: {
		...mapMutations(['logout']),
		
		
		
		loadData() {
			let opts = {
				url: '/productApi/cartList/',
				method: 'post'
			};
			let param = {};
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						this._getdata(res.data.cartList);
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							mask: true,
							duration: 1000
						});
					}
				},
				error => {
					console.log(error);
				}
			);
		},
		//请求数据
		async _getdata(list) {
			//let _list = await this.$api.json('cartList'); 
			let cartList = list.map(item => {
				item.checked = true;
				item.images=this.apiServer+item.images;
				return item;
			});
			
			this.cartList = cartList;
			this.calcTotal(); //计算总价
		},
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		navToLogin() {
			uni.navigateTo({
				url: '/pages/person/login'
			});
		},
		navtoproduct(psid){
			uni.navigateTo({
				url: '/pages/product/product?psid='+psid
			});
		},
		
		//选中状态处理
		check(type, index) {
			if (type === 'item') {
				this.cartList[index].checked = !this.cartList[index].checked;
			} else {
				const checked = !this.allChecked;
				const list = this.cartList;
				list.forEach(item => {
					item.checked = checked;
				});
				this.allChecked = checked;
			}
			this.calcTotal(type);
		},
		//数量
		numberChange(data) {
			this.cartList[data.index].number = data.number;
			let opts = {
				url: '/productApi/numberChange/',
				method: 'post'
			};
			let param = {id:this.cartList[data.index].id,number:data.number};
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						this.calcTotal();
					} else {
						uni.showToast({title: res.data.msg,icon: 'none'});
					}
				},
				error => {
					console.log(error);
				}
			);
			
			
			
			
		},
		//删除
		deleteCartItem(index) {
			let list = this.cartList;
			let row = list[index];
			let id = row.id;
			let opts = {
				url: '/productApi/deleteCartItem/',
				method: 'post'
			};
			let param = {id:id};
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						this.cartList.splice(index, 1);
						this.calcTotal();
						uni.hideLoading();
					} else {
						uni.showToast({title: res.data.msg,icon: 'none'});
					}
				},
				error => {
					console.log(error);
				}
			);
		},
		//清空
		clearCart() {
			uni.showModal({
				content: '清空购物车？',
				success: e => {
					if (e.confirm) {
						let opts = {
							url: '/productApi/clearCart/',
							method: 'post'
						};
						let param = {};
						http.httpTokenRequest(opts, param).then(
							res => {
								//打印请求返回的数据
								if (res.data['code'] == 0) {
									this.cartList = [];
								} else {
									uni.showToast({title: res.data.msg,icon: 'none'});
								}
							},
							error => {
								console.log(error);
							}
						);
						
					}
				}
			});
		},
		//计算总价
		calcTotal() {
			let list = this.cartList;
			if (list.length === 0) {
				this.empty = true;
				return;
			}
			let total = 0;
			let checked = true;
			list.forEach(item => {
				if (item.checked === true) {
					total += item.price * item.number;
				} else if (checked === true) {
					checked = false;
				}
			});
			this.allChecked = checked;
			this.total = Number(total.toFixed(2));
		},
		//创建订单
		createOrder() {
			let list = this.cartList;
			let goodsData = [];
			list.forEach(item => {
				if (item.checked) {
					goodsData.push(item.id);
				}
			});
			
			if(goodsData.length==0){
				uni.showToast({title:'没有选择结算的产品',icon: 'none'})
				return;
			}
			uni.navigateTo({
				url: `/pages/order/createOrder?data=${JSON.stringify({
					goodsData: goodsData
				})}`
			});
		}
	}
};
